body {
    background-color: #333;
    justify-content: center;
}
#input::placeholder {
    color: rgb(238, 238, 238);
}
::-webkit-scrollbar {
    display: none;
}
#title {
    margin: 0 9px 0 15px;
    font-size: 19px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
#subtitle {
    margin: 3px 9px 0 15px;
    color: grey;
    font-size: 13px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}
#onplaying {
    margin: 7px 5px 0 5px;
    font-size: 11px;
    font-weight: 400;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}